import React, {useState} from 'react';
import styles from "./Paginator.module.css";
import cn from "classnames";


let Paginator = ({totalUsersCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {

    let pagesCount = Math.ceil(totalUsersCount / pageSize);

    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount/portionSize);
    let [portionIndex, setPortionIndex] = useState(1);
    let leftPortionPageNumber= (portionIndex-1) * portionSize+1;
    let rightPortionPageNumber = portionIndex * portionSize
    return (
        <div className={styles.paginator}>
            {portionIndex > 1 &&
                <button onClick={() => {setPortionIndex(portionIndex-1)}}>PREV</button>
            }
            {pages.filter(p=> p >= leftPortionPageNumber && p<=rightPortionPageNumber).map((p) => {
                return <span className={ cn({
                    [styles.selectedPage]: currentPage === p
                }, styles.pageNumber)}
                            key={p}
                            onClick={(e) => {
                                onPageChanged(p)
                            }}
                            >{p}</span>
            })}
            {portionCount > portionIndex &&
                <button onClick={() => {setPortionIndex(portionIndex+1)}}>NEXT</button>}
        </div>
    );
}

export default Paginator;